/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* Container for calculators */
.container {
  padding: 20px;
  max-width: 800px;
  margin: auto;
}

/* Media queries for responsiveness */
@media (max-width: 600px) {
  .container {
    padding: 10px;
  }

  .MuiTypography-h4 {
    font-size: 1.5rem;
  }

  .MuiTextField-root {
    width: 100%;
    margin-bottom: 10px;
  }

  .MuiButton-root {
    width: 100%;
    margin-bottom: 10px;
  }
}
/* Highlighted row style */
.highlighted {
  background-color: #ffeb3b !important;
  color: #000;
}

